import { Injectable } from '@angular/core';

import { BaseAPIService } from '../base.service';
import { HashMap } from 'src/app/shared/utilities/types.utilities';

export interface PollingMetadata {
    room_id: string;
    metadata?: HashMap<any>;
}

export class PingHistory {
    /** List of previous poll data */
    readonly history: PollingMetadata[];
    /** Unix time in seconds of the last poll request */
    readonly last_ping: number;
    /** Email of the associated space */
    readonly room_email: string;
    /** ID of the associated space */
    readonly room_id: string;

    constructor(data: HashMap) {
        this.history = data.history;
        this.last_ping = data.last_ping;
        this.room_email = data.room_email;
        this.room_id = data.room_id;
    }
}

const SECONDS = 1000;
const MINUTES = 60 * SECONDS;

@Injectable({
    providedIn: 'root'
})
export class PollingService extends BaseAPIService<PingHistory> {
    constructor() {
        super();
        this._name = 'Users';
        this._api_route = 'pings';
    }

    protected async load() {
        this.interval('poll_endpoint', () => {
            this.add({
                room_id: this.parent.get('system')
            });
        }, 10 * MINUTES);
    }

    public query(...args: any): never {
        throw new Error('Query method not available for polling endpoint');
    }

    public show(...args: any): never {
        throw new Error('Show method not available for polling endpoint');
    }

    public update(...args: any): never {
        throw new Error('Put method not available for polling endpoint');
    }

    public delete(...args: any): never {
        throw new Error('delete method not available for polling endpoint');
    }

    protected process(data: HashMap) {
        return new PingHistory(data);
    }
}
