import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { first } from 'rxjs/operators';
import { PlaceSystem, PlaceModuleBinding, onlineState, getModule, querySystems } from '@placeos/ts-client';

import { BaseClass } from '../../../shared/base.class';

@Injectable({
    providedIn: 'root',
})
export class SystemsManagerService extends BaseClass {
    /** Subject for System list */
    private _list = new BehaviorSubject<PlaceSystem[]>([]);
    /** Observable for system list */
    public readonly systems = this._list.asObservable();

    constructor() {
        super();
        onlineState().pipe(first(_ => _)).subscribe(() => this.load());
    }

    /** List of available systems */
    public get list() {
        return this._list.getValue() || [];
    }

    /**
     * Get engine module for the given system
     * @param sys_id ID of the system to get the module from
     * @param mod_id ID of the module with the given system
     * @param index Index of the module
     */
    public get(sys_id: string, mod_id: string, index?: number): PlaceModuleBinding {
        return getModule(sys_id, mod_id, index);
    }

    /**
     * Load Systems
     */
    protected load() {
        this.loadSystems();
    }

    /**
     * Load one page of systems
     * @param offset Item offset for the page to load
     */
    protected loadSystems(offset: number = 0, limit: number = 2000) {
        querySystems({ offset, limit })
            .toPromise()
            .then((details) => {
                const systems: PlaceSystem[] = [...this.list, ...details.data];
                systems.sort((a, b) => (a.name || '').localeCompare(b.name));
                this._list.next(systems);
                if (systems.length < details.total) {
                    this.loadSystems(systems.length);
                }
            })
            .catch((e) => this.timeout('retry', () => this.loadSystems(offset), 2000));
    }
}
